.work__title {

	.title-bg {
		background-image      : url("../images/saving/programs/bg-title.svg");
		background-position-y : 6px;
		background-size       : contain;
		margin                : 0 -15px;
		padding               : 0px 15px;
		position              : relative;
		z-index               : 2;

		@media screen and (min-width : $break-table) {

			margin  : 0 -18px;
			padding : 0 25px;
		}

		@media screen and (min-width : $break-desktop) {
			background-position-y : 12px;
		}
	}
}

.work__grid {
	counter-reset         : count;
	display               : grid;
	grid-gap              : 8px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.card--work {
	background-color : $gray;
	border-radius    : 8px;
	grid-column      : 4 span;
	padding          : 24px 24px;
	position         : relative;

	&:before {
		color             : $red;
		content           : "0" counter(count);
		counter-increment : count;
		font-size         : 16px;
		font-style        : normal;
		font-weight       : 500;
		line-height       : 24px;
		position          : absolute;
		right             : 24px;
		top               : 24px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 28px;
			right       : 40px;
			top         : 40px;
		}
	}

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
	}
}


.work__application {
	align-items       : center;
	background-color  : $red;
	background-image  : url("../images/housing/work/cta.png");
	background-repeat : no-repeat;
	background-size   : cover;
	border-radius     : 8px;
	display           : flex;
	grid-column       : 4 span;
	justify-content   : center;
	padding           : 70px 0;

	@media screen and (min-width : $break-table) {
		background-image : url("../images/housing/work/cta-desktop.png");
		grid-column      : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/housing/work/cta.png");
		grid-column      : 4 span;
		padding          : 0;
	}
}
