.conditions__title {
	.title-bg {
		background-image      : url("../images/pts/conditions/title-bg.svg");
		background-position-y : 5px;
		margin                : 0 -20px;
		padding               : 0 20px;
	}
}

.conditions__grid {
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);
	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.card--conditions {
	grid-column : 4 span;
	margin      : 0;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
	}
}
