.about {
	padding : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 40px 0;
	}
}

.about__text {
	color       : $black;
	font-size   : 14px;
	font-style  : normal;
	font-weight : 500;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 32px;
			}
		}
	}
}
