
.advantages__title {

	@media screen and (min-width : $break-table) {
		max-width : 400px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 482px;
	}

	.title-bg {
		background-image      : url("../images/home/advantages/bg-title.svg");
		background-position-y : 12px;
		background-size       : contain;
		margin                : 0 -15px;
		padding               : 5px 15px;
		position              : relative;
		z-index               : 2;

		@media screen and (min-width : $break-table) {
			margin  : 0 -18px;
			padding : 0 25px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.advantages__title--business {
	max-width : 900px;
}

.advantages__wrap {
	align-items      : center;
	background-color : $gray;
	border-radius    : 8px;
	display          : flex;
	flex-direction   : column-reverse;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
	}
}

.advantages__list {
	padding : 22px 24px 24px 24px;

	@media screen and (min-width : $break-table) {
		max-width : 560px;
		padding   : 64px 0;
	}

	@media screen and (min-width : $break-desktop) {

		padding : 64px 15px 64px 64px;
	}

	li {
		color        : $black;
		font-size    : 14px;
		font-weight  : 500;
		line-height  : 20px;
		padding-left : 32px;
		position     : relative;

		@media screen and (min-width : $break-table) {
			font-size    : 20px;
			line-height  : 28px;
			padding-left : 54px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&:not(:last-child) {
			margin-bottom : 16px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 32px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		&:before {
			background-image : url("../images/icon/check-small.svg");
			background-size  : 20px 20px;
			content          : "";
			height           : 20px;
			left             : 0;
			position         : absolute;
			top              : 0;
			width            : 20px;

			@media screen and (min-width : $break-table) {
				background-size : 32px 32px;
				height          : 32px;
				width           : 32px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}

.advantages__img {
	padding    : 18px 18px 0 18px;
	text-align : center;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 45px 45px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 45px 64px 45px 0;
	}
}
