.header {
	background-color : $white;
	box-shadow       : 0px 8px 16px 0px rgba(0, 0, 0, 0.08), 0px 0px 4px 0px rgba(0, 0, 0, 0.04);
	margin-bottom    : 24px;
	padding          : 16px 0;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
		padding       : 24px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.header__wrapper {
	align-items     : center;
	display         : flex;
	justify-content : space-between;

	@media screen and (min-width : $break-table) {
		display               : grid;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		justify-content : flex-start;
	}

	.b-btn--red {
		display     : none;
		margin-left : auto;

		@media screen and (min-width : $break-table) {
			display     : inline-flex;
			grid-column : 10/13;
		}
	}
}

.header__panel {
	background-color : #FFF;
	display          : flex;
	flex-direction   : column;
	height           : calc(100dvh - 70px);
	justify-content  : flex-start;
	opacity          : 0;
	overflow-x       : hidden;
	overflow-y       : auto;
	position         : absolute;
	right            : -100%;
	top              : 70px;
	transition       : visibility .3s ease, opacity .3s ease, right .3s ease;
	visibility       : hidden;
	width            : 100%;
	z-index          : 100;

	&.active {
		opacity    : 1;
		right      : 0;
		visibility : visible;
	}

	@media screen and (min-width : $break-table) {
		border-top  : 1px solid $gray-stroke;
		display     : block;
		grid-column : 1/13;
		grid-row    : 2/2;
		height      : auto;
		margin-top  : 24px;
		opacity     : 1;
		overflow-x  : visible;
		overflow-y  : visible;
		padding-top : 24px;
		position    : unset;
		right       : unset;
		top         : unset;
		visibility  : visible;
	}

	@media screen and (min-width : $break-desktop) {
		border-top  : none;
		margin-left : 45px;
		margin-top  : 0;
		padding-top : 0;
	}
}

.logo {
	align-items : center;
	display     : inline-flex;

	img {
		height : 36px;
		width  : 95px;

		@media screen and (min-width : $break-table) {
			height : 40px;
			width  : 106px;
		}

		@media screen and (min-width : $break-desktop) {

		}

	}

	span {
		color       : $black;
		display     : inline-block;
		font-size   : 10px;
		font-weight : 500;
		line-height : 12px;
		margin-left : 16px;
		max-width   : 127px;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 20px;
			margin-left : 24px;
			max-width   : 218px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.header__logo {
	grid-column : 1/7;

	@media screen and (min-width : $break-desktop) {
		flex-shrink : 0;
	}
}

.nav {
	display         : flex;
	flex-direction  : column;
	justify-content : flex-start;
	padding         : 33px 33px;
	width           : 100%;

	@media screen and (min-width : $break-table) {
		display : block;
		padding : 0;
	}

	&__list {

		@media screen and (min-width : $break-table) {
			align-items     : center;
			display         : flex;
			justify-content : space-between;
		}

		@media screen and (min-width : $break-desktop) {
			justify-content : flex-start;
		}
	}

	&__item {
		position : relative;
		z-index  : 4;

		& + & {

			@media screen and (min-width : $break-table) {
				margin-left : 32px;
			}
		}

		&:first-child {
			.nav__link {
				color       : #666;
				font-size   : 14px;
				font-weight : 500;
				line-height : 20px;

				svg {
					display : none;
				}

				@media screen and (min-width : $break-table) {
					color       : $black;
					display     : inline-flex;
					font-size   : 16px;
					font-weight : 500;
					line-height : 24px;
					svg {
						display : block;
					}
				}
			}
		}

		&:not(:first-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 0;
			}
		}

		&:last-child {
			margin-bottom : 0;
		}

		&:hover {
			.nav__submenu {

				@media screen and (min-width : $break-table) {
					opacity    : 1;
					visibility : visible;
				}
			}

			.nav__link {
				color : $red;

				svg {
					transform : rotate(180deg);

					path {
						stroke : $red;
					}
				}
			}
		}
	}

	&__link {
		align-items : center;
		color       : $black;
		display     : inline-flex;
		font-size   : 16px;
		font-weight : 500;
		line-height : 24px;
		position    : relative;
		transition  : color .3s ease;
		z-index     : 4;

		&:hover {
			color : $red;
		}

		svg {
			flex-shrink : 0;
			margin-left : 9px;

			transition  : stroke .3s ease, transform .3s ease;

			path {
				stroke : $black;
			}
		}
	}
}

.nav__submenu {
	border-bottom  : 1px solid $gray-stroke;
	margin-bottom  : 32px;
	padding-bottom : 32px;


	@media screen and (min-width : $break-table) {
		border-bottom  : none;
		left           : 0;
		margin-bottom  : 0;
		max-height     : none;
		opacity        : 0;
		overflow       : visible;
		padding-bottom : 0;
		padding-top    : 61px;
		position       : absolute;
		top            : 0;
		transition     : visibility .3s ease, opacity .3s ease;
		visibility     : hidden;
		z-index        : 3;
	}

	ul {
		background-color : $white;
		border-radius    : 8px;

		li {


			@media screen and (min-width : $break-table) {
				min-width : 321px;
			}

			a {
				display : flex;
				padding : 12px 0;

				@media screen and (min-width : $break-table) {
					padding : 15px 15px;
				}

				span {
					color        : $black;
					font-size    : 16px;
					font-weight  : 500;
					line-height  : 24px;
					padding-left : 10px;
					transition   : color .3s ease;

					&:hover {
						color : $red;
					}
				}
			}
		}
	}
}

.burger {
	background-color : transparent;
	border           : none;
	color            : $black;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 16px;
	padding          : 0;
	position         : relative;
	width            : 24px;

	@media screen and (min-width : $break-table) {
		display : none;
	}

	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 2px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&::before {
		top : 0;
	}

	&::after {
		top : calc(100% - 2px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 2px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

	&--active {
		&::before {
			top        : 50%;
			transform  : rotate(45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		&::after {
			top        : 50%;
			transform  : rotate(-45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		.burger__line {
			transform  : scale(0);
			transition : transform 0.3s ease-in-out;
		}
	}
}
