@font-face {
	font-family  : 'TTHoves';
	src          : url('../fonts/TTHoves-Medium.woff2') format('woff2'),
	url('../fonts/TTHoves-Medium.woff') format('woff'),
	url('../fonts/TTHoves-Medium.ttf') format('truetype');
	font-weight  : 500;
	font-style   : normal;
	font-display : swap;
}

@font-face {
	font-family  : 'TTHoves';
	src          : url('../fonts/TTHoves-Bold.woff2') format('woff2'),
	url('../fonts/TTHoves-Bold.woff') format('woff'),
	url('../fonts/TTHoves-Bold.ttf') format('truetype');
	font-weight  : 700;
	font-style   : normal;
	font-display : swap;
}
