.stages__title {
	.title-bg {
		background-image      : url("../images/pts/stages/bg-title.svg");
		background-position-y : 5px;
		margin                : 0 -20px;
		padding               : 0 20px;
		position              : relative;
		white-space           : nowrap;
		z-index               : -1;
	}
}

.stages__grid {
	display               : grid;
	grid-gap              : 8px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}

}

.stages__main {
	background-color : $gray;
	border-radius    : 8px;
	grid-column      : 4 span;
	grid-row         : 2/2;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/6;
		grid-row    : 1/3;
		padding     : 64px 38px 64px 64px;
	}
}

.stages__list {
	counter-reset   : count;
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : space-between;

	li {
		color          : $black;
		display        : flex;
		flex-direction : column;
		font-size      : 16px;
		font-style     : normal;
		font-weight    : 500;
		line-height    : 24px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 28px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&:not(:last-child) {
			margin-bottom : 33px;
		}

		&:before {
			color             : #C3121B;
			content           : "0" counter(count);
			counter-increment : count;
			font-style        : normal;
			margin-bottom     : 16px;
			right             : 24px;
			top               : 24px;
			z-index           : 1;
		}
	}
}

.stages__img {
	background-color : $gray;
	border-radius    : 8px;
	grid-column      : 4 span;
	grid-row         : 1/1;
	padding          : 24px 16px;
	text-align       : center;
	@media screen and (min-width : $break-table) {
		padding : 40px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6/13;
		grid-row    : 1/2;
		padding     : 57px 36px;
	}
}

.stages__wrap {
	align-items       : center;
	background-color  : #C3121B;
	background-image  : url("../images/pts/stages/stages-bg-mob.png");
	background-repeat : no-repeat;
	background-size   : cover;
	border-radius     : 8px;
	display           : flex;
	grid-column       : 4 span;
	grid-row          : 3/3;
	justify-content   : center;
	padding           : 70px 0;

	@media screen and (min-width : $break-table) {
		background-image : url("../images/pts/stages/stages-bg-desktop.png");
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6/13;
		grid-row    : auto;
		padding     : 53px 0;
	}
}
