// -----------------------------------------------
//  Form config
// -----------------------------------------------
// Error
$form-label-color  : #A7A8B1;
$form-label-size   : 12px;
$form-label-weight : 400;
$form-label-margin : 0 0 10px;
$form-error-color  : $red;
$form-error-size   : 13px;
$form-error-weight : 500;

// Hint
$form-hint-color   : #444;
$form-hint-size    : 12px;
$form-hint-weight  : 400;
$form-hint-style   : italic;
$form-hint-margin  : 5px 0 0;

// Grid
$form-grid         : 7px;

// -----------------------------------------------
//  Form style
// -----------------------------------------------
.form__row {
	margin-top : 20px;
	position   : relative;

	&:first-child {
		margin-top : 0;
	}

	input[type="text"],
	input[type="email"],
	input[type="search"],
	input[type="number"],
	input[type="tel"],
	input[type="password"],
	select,
	textarea {
		vertical-align : top;
		width          : 100%;
	}

	> label:not(.checkbox-style) {
		align-items     : center;
		color           : $form-label-color;
		cursor          : pointer;
		display         : flex;
		flex-flow       : row nowrap;
		font-size       : $form-label-size;
		font-weight     : $form-label-weight;
		justify-content : space-between;
		line-height     : 1.2;
		margin          : $form-label-margin;

		> span {
			order : 0;
		}

		.error-message {
			margin-left : 10px;
			margin-top  : 0;

			+ span {
				color : $red;
			}
		}
	}
}

.form__wrapper {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.form__group {
	display               : grid;
	grid-column           : 4 span;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column           : 12 span;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.form__wrap {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.form__label {
	display        : flex;
	flex-direction : column;
	width          : 100%;
}

.form__item {
	grid-column : 6 span;
}

.form__item--error {
	.form__input {
		border : 1px solid $red;
	}

	.form__error {
		height     : auto;
		opacity    : 1;
		visibility : visible;
	}
}

.form__name {
	color         : #000;
	font-size     : 14px;
	font-style    : normal;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 16px;
	}
}

.form__textarea {
	resize : none;
}

.form__input {
	position : relative;
	z-index  : 1;

	&.just-validate-error-field {
		border : 1px solid $red;
	}
}

.form__hint {
	color       : $form-hint-color;
	font-size   : $form-hint-size;
	font-style  : $form-hint-style;
	font-weight : $form-hint-weight;
	margin      : $form-hint-margin;
	position    : relative;
	z-index     : 2;
}

.form__error {
	color       : $red;
	font-size   : $form-error-size;
	font-weight : $form-error-weight;
	height      : 0;
	margin-top  : 3px;
	opacity     : 0;
	position    : relative;
	visibility  : hidden;
	z-index     : 3;
}

.application-form {
	background-color : $gray;
	border-radius    : 8px;
	padding          : 24px 24px;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.section-title {
		margin-bottom : 24px;
		text-align    : center;

		@media screen and (min-width : $break-table) {
			margin-bottom : 40px;
		}

		.title-bg {
			background-image      : url('../images/business/for-clients/bg-title.svg');
			background-position-y : 5px;
			background-size       : contain;
			margin                : 0 -15px;
			padding               : 5px 15px;
			position              : relative;
			white-space           : nowrap;
			z-index               : 2;
		}
	}
}

.form--application {
	border-bottom  : 1px solid rgba(0, 0, 0, 0.20);
	padding-bottom : 28px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 40px;
	}

	.b-btn--red {
		left       : 50%;
		margin-top : 24px;
		min-width  : 100%;
		position   : relative;
		transform  : translateX(-50%);

		@media screen and (min-width : $break-table) {
			margin-top : 40px;
			min-width  : auto;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.application-form__wrap {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	padding-top    : 24px;

	@media screen and (min-width : $break-table) {
		padding-top : 40px;
	}
}

.application-form__text {
	color         : $black;
	font-size     : 14px;
	font-style    : normal;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 8px;
	}
}

.application-form__link {
	align-items : center;
	color       : $red;
	display     : inline-flex;
	font-size   : 24px;
	font-style  : normal;
	font-weight : 500;
	line-height : 32px;
	position    : relative;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 40px;
	}

	&:before {
		background-image  : url("../images/icon/phone-call.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : block;
		height            : 32px;
		margin-right      : 8px;
		width             : 32px;

		@media screen and (min-width : $break-table) {
			height : 40px;
			width  : 40px;
		}
	}
}

.form-password {
	position : relative;

	input[type="text"],
	input[type="password"] {
		padding-right : 50px;
		position      : relative;
		z-index       : 1;
	}
}

.form-password__button {
	background                  : transparent;
	border                      : 0;
	bottom                      : 0;
	-moz-box-sizing             : border-box;
	-webkit-box-sizing          : border-box;
	box-sizing                  : border-box;
	cursor                      : pointer;
	opacity                     : .3;
	outline                     : 0;
	padding                     : 0;
	position                    : absolute;
	right                       : 0;
	-webkit-tap-highlight-color : transparent;
	top                         : 0;
	transition                  : opacity .3s ease;
	vertical-align              : middle;
	width                       : 42px;
	z-index                     : 10;

	i {
		position       : relative;
		vertical-align : middle;
		z-index        : 1;
	}

	&:after {
		background : #000;
		border-top : 1px solid #FFF;
		content    : '';
		display    : block;
		height     : 1px;
		left       : 9px;
		position   : absolute;
		top        : 24px;
		transform  : rotate(-30deg);
		transition : opacity .3s ease;
		width      : 24px;
		z-index    : 10;
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		&:hover {
			opacity : 1;
		}
	}

	&.active {
		opacity : 1;

		&:after {
			opacity : 0;
		}
	}
}
