.guarantees__title {
	.title-bg {
		background-image      : url("../images/saving/programs/bg-title.svg");
		background-position-y : 10px;
		background-size       : contain;
		margin                : 0 -15px;
		padding               : 0 15px;
		position              : relative;
		z-index               : 2;

		@media screen and (min-width : $break-table) {
			margin  : 0 -18px;
			padding : 0 25px;
		}
	}
}

.guarantees__wrap {

}


.swiper--guarantees {

	.swiper-wrapper {
		@media screen and (min-width : $break-table) {
			box-sizing            : unset;
			display               : grid;
			grid-gap              : 24px;
			grid-template-columns : repeat(12, 1fr);
			transform             : translate3d(0, 0, 0) !important;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.swiper-slide {
		display : flex;
		height  : auto;

		@media screen and (min-width : $break-table) {
			grid-column : 6 span;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 3 span;
		}
	}
}
