.hero {
	padding-bottom : 24px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 40px;
	}
}

.hero__wrapper {
	background-color : $gray;
	border-radius    : 8px;
	padding          : 24px 24px 0 24px;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px 0 64px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
		padding         : 0;
	}

	&--saving {
		padding : 24px 24px;

		@media screen and (min-width : $break-table) {
			padding : 40px 64px;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 48px 64px;
		}
	}
}

.hero__left {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		max-width     : 600px;
		padding       : 64px 0 64px 64px;
	}

	&--saving {
		@media screen and (min-width : $break-table) {
			margin-bottom : 16px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 0;
			max-width     : none;
			padding       : 0;
		}
	}

	&--pts {
		@media screen and (min-width : $break-table) {
			margin-bottom : 16px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 0;
			padding       : 0;
		}

		.hero__text {
			margin-bottom : 24px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 64px;
				max-width     : 350px;
			}
		}
	}

	.b-btn--red {
		min-width : 100%;

		@media screen and (min-width : $break-table) {
			min-width : auto;
			padding   : 16px 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.hero__text {
	color       : $black;
	font-size   : 20px;
	font-style  : normal;
	font-weight : 500;
	line-height : 28px;
}

.hero__title {
	color         : $black;
	font-size     : 22px;
	font-weight   : 500;
	line-height   : 32px;
	margin-bottom : 24px;
	position      : relative;
	z-index       : 3;

	@media screen and (min-width : $break-table) {
		font-size     : 48px;
		line-height   : 56px;
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.title-bg {
		background-image : url("../images/home/hero/bg-title.svg");
		background-size  : cover;
		margin           : 0 -5px;
		padding          : 0 10px;
		position         : relative;
		z-index          : 2;

		@media screen and (min-width : $break-table) {
			background-position-y : 10px;
			background-size       : contain;
			margin                : 0 -18px;
			padding               : 0 25px;
		}

		&--about {
			background-image : url("../images/home/hero/bg-title-about.svg");

			@media screen and (min-width : $break-table) {
				background-position-y : 5px;
				background-size       : contain;
				margin                : 0 -18px;
				padding               : 0 25px;
			}
		}
	}
}

.hero__list {
	margin-bottom : 28px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--saving {
		.hero__item {

		}
	}

}

.hero__item {
	align-items : start;
	color       : $black;
	display     : flex;
	font-size   : 14px;
	font-weight : 500;
	line-height : 20px;
	position    : relative;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--saving {
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			max-width : 519px;
		}
	}

	a {
		color       : $black;
		font-size   : 14px;
		font-weight : 500;
		line-height : 20px;
		position    : relative;

		&:after {
			background-color : $black;
			bottom           : 2px;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			transition       : width .3s ease;
			width            : 0;
			z-index          : 1;
		}

		&:hover {
			&:after {
				width : 100%;
			}
		}

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 28px;
		}
	}

	&:before {
		background-color : $red;
		border-radius    : 100%;
		content          : "";
		display          : inline-block;
		height           : 8px;
		margin-right     : 12px;
		margin-top       : 10px;
		width            : 8px;
	}

	&:not(:last-child) {
		margin-bottom : 12px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 16px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.hero__img {
	display         : flex;
	flex-direction  : column;
	justify-content : flex-end;
	text-align      : center;
}
