// Arrow
.swiper-button-prev,
.swiper-button-next {
	align-items                 : center;
	background-color            : $light-red;
	border                      : none;
	border-radius               : 64px;
	cursor                      : pointer;
	display                     : flex;
	height                      : 44px;
	justify-content             : center;
	outline                     : 0;
	padding                     : 0;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	transition                  : background-color .3s ease;
	user-select                 : none;
	vertical-align              : top;
	width                       : 68px;

	&:after {
		content  : '';
		left     : 0;
		position : absolute;
		top      : 0;
	}

	@media screen and (min-width : 1076px) {
		&:hover {
			background-color : #820C12;

			svg {
				path {
					fill : $white;
				}
			}
		}
	}

	svg {
		path {
			fill       : $red;
			transition : fill .3s ease;
		}
	}

	&.swiper-button-disabled {
		cursor         : auto;
		opacity        : 0.3;
		pointer-events : none;
	}
}

