.membership {
	padding-bottom : 24px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 40px;
	}
}

.membership__main {
	align-items      : center;
	background-color : $gray;
	border-radius    : 8px;
	display          : flex;
	flex-direction   : column-reverse;
	margin-bottom    : 24px;
	padding          : 19px 19px;

	@media screen and (min-width : $break-table) {
		padding : 0 64px 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		flex-direction  : row;
		justify-content : space-between;
		padding         : 0 64px;
	}
}

.membership__title {
	color       : $black;
	font-size   : 32px;
	font-weight : 500;
	line-height : 40px;


	@media screen and (min-width : $break-table) {
		font-size   : 48px;
		line-height : 56px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 515px;
	}

	.title-bg {
		background-image      : url("../images/membership/bg-title.svg");
		background-position-y : 5px;
		margin                : 0 -20px;
		padding               : 0 20px;
	}
}

.membership__img {
	margin-bottom : 12px;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}
}

.membership__wrap {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		max-width : 851px;
	}
}

.membership__list {
	&:not(:last-child) {
		margin-bottom : 32px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.membership__name {
	color         : $black;
	font-size     : 24px;
	font-weight   : 500;
	line-height   : 32px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size     : 32px;
		line-height   : 40px;
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.membership__subname {
	color         : $black;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 32px;
	margin-bottom : 24px;

}

.membership__group {
	&:not(:last-child) {
		margin-bottom : 32px;
	}
}

.membership__item {
	color        : $black;
	font-size    : 14px;
	font-weight  : 500;
	line-height  : 20px;
	padding-left : 32px;
	position     : relative;

	@media screen and (min-width : $break-table) {
		font-size    : 16px;
		line-height  : 24px;
		padding-left : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		background-image  : url("../images/icon/check-small.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 20px;
		left              : 0;
		position          : absolute;
		top               : 0;
		width             : 20px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			height : 24px;
			width  : 24px;
		}
	}

	&:not(:last-child) {
		margin-bottom : 18px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 24px;
		}
	}
}

.membership__text {
	color       : $black;
	font-size   : 16px;
	font-weight : 500;
	line-height : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	&--large {
		font-size   : 20px;
		line-height : 26px;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	p {
		&:not(:last-child) {
			margin-bottom : 20px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 25px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}

.tabs {


	&__list {
		align-items      : center;
		background-color : $gray;
		border-radius    : 8px;
		display          : flex;
		margin           : 0 -24px 48px -24px;
		margin-bottom    : 64px;
		overflow-x       : auto;
		padding          : 18px 24px;

		@media screen and (min-width : $break-table) {
			justify-content : center;
			margin          : 0 0 64px 0;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 18px 0;
		}
	}

	&__button {
		background-color : transparent;
		border           : none;
		color            : $black;
		font-size        : 16px;
		font-weight      : 500;
		line-height      : 24px;
		min-width        : 92px;
		padding          : 0;
		position         : relative;

		@media screen and (min-width : $break-desktop) {
			min-width : auto;
		}

		&:not(:first-child) {
			margin-left : 23px;

			@media screen and (min-width : $break-table) {
				margin-left : 16px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-left : 32px;
			}
		}


		&.active {
			&:before {
				background-color : $red;
				bottom           : -16px;
				content          : "";
				height           : 2px;
				left             : 0;
				position         : absolute;
				width            : 100%;
				z-index          : 1;
			}
		}
	}

	&__container {
		display : none;

		&.active {
			display : block;
		}
	}
}

.red {
	color : $red;
}
