.documents__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.documents__item {

	width : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 12px;
	}
}

.documents__head {
	.swiper-nav {
		display : none;
	}
}

.documents__wrap {
	margin : 0 -24px;

	@media screen and (min-width : $break-table) {
		margin : 0 -40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -12px;
	}
}

.swiper--documents {

	.swiper-slide {
		display : flex;
		height  : auto;
		margin  : 0 8px;
		width   : 70%;

		@media screen and (min-width : $break-table) {
			margin : 0 12px;
		}

		@media screen and (min-width : $break-desktop) {
			margin : 0;
		}

		&:first-child {
			margin-left : 24px;

			@media screen and (min-width : $break-table) {
				margin-left : 40px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-left : 0;
			}

		}

		&:last-child {
			margin-right : 24px;

			@media screen and (min-width : $break-table) {
				margin-right : 40px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-right : 0;
			}
		}

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 25%;
		}
	}
}
