.select__wrapper {
	height        : 70px;
	margin-bottom : 20px;
	position      : relative;
	z-index       : 10;


	&.active {
		.select__head {
			&:after {
				transform : rotate(180deg);
			}
		}

		.select__list {
			border-bottom-left-radius  : 12px;
			border-bottom-right-radius : 12px;
			border-top                 : none;
			opacity                    : 1;
			transform                  : scaleY(1) translateX(-50%);
		}
	}
}


.select__head {
	align-items      : center;
	background-color : $white;
	border           : 1px solid rgba(0, 0, 0, 0.20);
	border-radius    : 8px;
	cursor           : pointer;
	display          : flex;
	font-size        : 16px;
	font-weight      : 500;
	justify-content  : space-between;
	line-height      : 24px;
	padding          : 16px 16px;

	&:after {
		background-image  : url("../images/icon/chevron-down.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : block;
		height            : 16px;
		transform         : rotate(0deg);
		transition        : transform .3s ease;
		width             : 16px;
	}

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
		padding     : 18px 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.select__list {
	background-color : $white;
	border           : 1px solid rgba(0, 0, 0, 0.20);
	border-radius    : 8px;
	left             : 50%;
	margin-top       : 10px;
	opacity          : 0;
	padding          : 18px 24px;
	position         : absolute;
	transform        : scaleY(0) translateX(-50%);
	transform-origin : 0 0;
	transition       : all .3s ease;
	width            : 100%;
}

.radio-hidden {
	border            : 0;
	clip              : rect(0, 0, 0, 0);
	-webkit-clip-path : inset(100%);
	clip-path         : inset(100%);
	height            : 1px;
	margin            : -1px;
	overflow          : hidden;
	padding           : 0;
	position          : absolute;
	width             : 1px;
}

.radio-label {
	border-radius : 6px;
	cursor        : pointer;
	display       : inline-block;
	font-size     : 16px;
	font-weight   : 400;
	padding       : 0.5em 1em;
	width         : 100%;
}

.radio-hidden:focus + .radio-label {

}

.radio-hidden:checked + .radio-label {
	background-color : $red;
	border-color     : transparent;
	color            : #FFF;
}
