.loan__title {
	.title-bg {
		background-image      : url("../images/pts/loan/bg-title.svg");
		background-position-y : 5px;
		margin                : 0 -30px;
		padding               : 0 30px;
		position              : relative;
		white-space           : nowrap;
		z-index               : -1;
	}
}

.loan__grid {
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);
	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.card--loan {
	grid-column : 4 span;
	margin      : 0;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
	}
}
