* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

*,
*::before,
*::after {
	box-sizing : inherit;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	min-height     : 100%;
	overflow       : hidden;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main {
	flex : 1 0 auto;
}

.main--offset {
	border        : 1px solid #CCD9E5;
	border-radius : 8px;
}

.container {
	margin    : 0 auto;
	max-width : 1368px;
	padding   : 0 24px;
	width     : 100%;

	@media screen and (min-width : $break-table) {
		max-width : 1400px;
		padding   : 0 40px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.section-head {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.box-item {
	border-radius : 30px;
	padding       : 40px 40px;

	@media screen and (min-width : $break-desktop) {
		padding : 64px 64px;
	}

	&--background {

	}

	&--nopadding {
		padding : 0;
	}
}


.section-title {
	font-size   : 32px;
	font-style  : normal;
	font-weight : 600;
	line-height : 40px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		font-weight : 600;
		line-height : 56px;
	}
}

.scroll {
	height   : 100vh;
	overflow : hidden;
	position : relative;
	width    : 100vw;
}

.title-bg {
	background-position : center center;
	background-repeat   : no-repeat;
	background-size     : cover;
}
