.spend__title {


	@media screen and (min-width : $break-table) {
		max-width : 500px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 640px;
	}

	.title-bg {
		background-image      : url("../images/saving/programs/bg-title.svg");
		background-position-y : 15px;
		background-size       : contain;
		margin                : 0 -15px;
		padding               : 5px 15px;
		position              : relative;
		z-index               : 2;

		@media screen and (min-width : $break-table) {
			margin  : 0 -18px;
			padding : 0 25px;
		}
	}
}

.spend__wrap {
	margin : 0 -24px;

	@media screen and (min-width : $break-table) {
		margin : 0 -40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0;
	}
}


.swiper--spend {
	.swiper-wrapper {
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			box-sizing            : unset;
			display               : grid;
			grid-gap              : 24px;
			grid-template-columns : repeat(12, 1fr);
			transform             : translate3d(0, 0, 0) !important;
		}
	}

	.swiper-slide {
		display : flex;
		height  : auto;

		&:first-child {

			.card {

				@media screen and (min-width : $break-table) {
					margin-left : 40px;
				}

				@media screen and (min-width : $break-desktop) {
					margin-left : 0;
				}
			}
		}

		&:last-child {
			.card {

				@media screen and (min-width : $break-table) {
					margin-right : 40px;
				}

				@media screen and (min-width : $break-desktop) {
					margin-right : 0;
				}
			}
		}


		@media screen and (min-width : $break-table) {
			width : 40%;

			.card--spend {
				margin : 0 24px;
			}
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 3 span;
			width       : 100%;
			.card--spend {
				margin : 0;
			}
		}
	}
}
