// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 500;
$btn-line-height           : 1.25;
$btn-border-width          : 1px;
$btn-padding               : 16px 32px;
$btn-font-size             : 16px;
$btn-border-radius         : 64px;
$btn-transition            : background .3s ease, color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 14px 30px;
$btn-font-size-big         : 16px;
// Small
$btn-padding-small         : 8px 30px;
$btn-font-size-small       : 13px;

// COLOR

// Red
$btn-red-bg                : #C3121B;
$btn-red-color             : #FFF;
$btn-red-bg-hover          : #820C12;
$btn-red-color-hover       : #FFF;

// White
$btn-white-bg              : #FFF;
$btn-white-color           : #C3121B;
$btn-white-bg-hover        : #FFF;
$btn-white-color-hover     : #C3121B;

// Ligth
$btn-light-bg              : #F9E7E8;
$btn-light-color           : #C3121B;
$btn-light-bg-hover        : #820C12;
$btn-light-color-hover     : #FFF;


// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	align-items        : center;
	border             : $btn-border-width solid transparent;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	display            : inline-flex;
	flex-flow          : row nowrap;
	font-size          : $btn-font-size;
	font-weight        : $btn-font-weight;
	justify-content    : center;
	line-height        : $btn-line-height;
	padding            : $btn-padding;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	transition         : $btn-transition;
	user-select        : none;
	vertical-align     : middle;
	white-space        : nowrap;

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR
	// Red
	&--red {
		background-color : $btn-red-bg;
		color            : $btn-red-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-red-bg-hover;
			color            : $btn-red-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-red-bg;
				color            : $btn-red-color;
			}
		}
	}

	&--light {
		background-color : $btn-light-bg;
		color            : $btn-light-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-light-bg-hover;
			color            : $btn-light-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-light-bg;
				color            : $btn-light-color;
			}
		}
	}

	&--white {
		background-color : $btn-white-bg;
		color            : $btn-white-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-white-bg-hover;
			color            : $btn-white-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-white-bg;
				color            : $btn-white-color;
			}
		}
	}


	// FULL-SIZE
	&--full-size {
		display : block;
		width   : 100%;

		+ .full-size {
			margin-right : $btn-spacing-y;
		}
	}

	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
