
.offers__wrap {
	margin : 0 -24px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}
}

.offers__title {
	.title-bg {
		background-image      : url("../images/home/offers/bg-title.svg");
		background-position-y : 5px;
		margin                : 0 -15px;
		padding               : 0 20px;
	}
}

.offers__item {
	grid-column : 3 span;
	width       : 100%;
}

.swiper--offers {
	overflow : visible;

	@media screen and (min-width : $break-table) {
		overflow : hidden;
	}

	.swiper-wrapper {

		@media screen and (min-width : $break-table) {
			box-sizing            : unset;
			display               : grid;
			grid-gap              : 24px;
			grid-template-columns : repeat(12, 1fr);
			transform             : translate3d(0, 0, 0) !important;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.swiper-slide {
		display : flex;
		height  : auto;
		margin  : 0 8px;
		width   : 70%;

		@media screen and (min-width : $break-table) {
			margin : 0;
			width  : 100%;
		}

		&:first-child {
			margin-left : 24px;

			@media screen and (min-width : $break-table) {
				margin-left : 0;
			}
		}

		&:last-child {
			margin-right : 24px;

			@media screen and (min-width : $break-table) {
				margin-right : 0;
			}
		}

		@media screen and (min-width : $break-table) {
			grid-column : 6 span;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 3 span;
		}
	}
}
