
.overlay {
	background : rgba(0, 0, 0, 0.7);
	bottom     : 0;
	left       : 0;
	opacity    : 0;
	position   : fixed;
	right      : 0;
	//z-index: -1;
	top        : 0;
	transition : opacity 0.3s ease, visibility .3s ease;
	visibility : hidden;
	z-index    : 101;

	&.active {
		opacity    : 1;
		visibility : visible;
	}
}

.popup {
	display    : none;
	left       : 50%;
	max-width  : 384px;
	padding    : 0 15px;
	//z-index: -3;
	position   : fixed;
	top        : 50%;
	transform  : translate(-50%, -50%);
	transition : opacity 0.5s ease;
	//opacity: 0;
	width      : 100%;
	z-index    : 103;

	@media screen and (min-width : $break-table) {
		max-width : 694px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 854px;
	}

	&.active {
		display    : block;
		//opacity: 1;
		transition : opacity 0.5s ease;
		//transform: translate(-50%, -45%);
		z-index    : 103;
	}

	&__wrap {
		align-items      : center;
		background-color : #F7F7F7;
		border-radius    : 8px;
		display          : flex;
		min-height       : 220px;
		overflow         : hidden;
		position         : relative;
	}

	&__close {
		background-color : transparent;
		border           : none;
		height           : 24px;
		padding          : 0;
		position         : absolute;
		right            : 26px;
		top              : 12px;
		width            : 24px;
		z-index          : 999;

		svg {
			height : 24px;
			width  : 24px;

			@media screen and (min-width : $break-table) {
				height : 32px;
				width  : 32px;
			}
		}

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			right : 34px;
			top   : 24px;
		}
	}
}


.connection {

}


.stage {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	width          : 100%;
}


.stage__text {
	color         : #000;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 16px;
	max-width     : 188px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		font-weight   : 500;
		line-height   : 24px;
		margin-bottom : 8px;
		max-width     : none;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.stage__link {
	color       : $red;
	display     : flex;
	font-size   : 24px;
	font-weight : 500;
	line-height : 32px;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		font-weight : 500;
		line-height : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		background-image  : url("../images/icon/phone-call.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : block;
		height            : 32px;
		margin-right      : 8px;
		width             : 32px;

		@media screen and (min-width : $break-table) {
			height : 40px;
			width  : 40px;
		}
	}
}


.success {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	padding        : 64px 25px;
	text-align     : center;
	width          : 100%;

	@media screen and (min-width : $break-table) {
		padding : 64px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.b-btn--red {
		width : 100%;

		@media screen and (min-width : $break-table) {
			width : auto;
		}
	}
}

.success__icon {
	height        : 80px;
	margin-bottom : 20px;
	width         : 80px;

	@media screen and (min-width : $break-table) {
		height        : 120px;
		margin-bottom : 48px;
		width         : 120px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.success__title {
	color         : $black;
	font-size     : 24px;
	font-weight   : 500;
	line-height   : 32px;
	margin-bottom : 24px;
	max-width     : 231px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 24px;
		max-width     : 323px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.title-bg {
		background-image      : url(../images/icon/success-bg.svg);
		background-position-y : 5px;
		margin                : 0 -15px;
		padding               : 0 25px;
	}
}

.success__text {
	color         : $black;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 24px;
	max-width     : 249px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 40px;
		max-width     : 294px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
