.additionally__title {
	.title-bg {
		background-image      : url("../images/business/aditionally/bg-title.svg");
		background-position-y : 5px;
		background-size       : contain;
		margin                : 0 -15px;
		padding               : 5px 15px;
		position              : relative;
		z-index               : 2;

		@media screen and (min-width : $break-table) {
			margin  : 0 -18px;
			padding : 0 25px;
		}
	}
}

.additionally__name {
	color         : $black;
	font-size     : 16px;
	font-style    : normal;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.additionally__list {
	counter-reset   : item;
	list-style-type : none;
	margin          : 0 0 0 -25px;

	li {
		color       : $black;
		display     : flex;
		font-size   : 14px;
		font-style  : normal;
		font-weight : 500;
		line-height : 20px;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&:not(:last-child) {
			margin-bottom : 5px;
		}

		&:before {
			content           : counter(item) '.';
			counter-increment : item;
			display           : block;
			margin-right      : 5px;
		}
	}
}

.accordion--additionally {
	padding : 24px 24px 0 24px;

	@media screen and (min-width : $break-table) {
		padding : 24px 40px 0 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
