.participate__wrapper {
	background-color      : $gray;
	border-radius         : 8px;
	display               : grid;
	grid-gap              : 40px;
	grid-template-columns : repeat(4, 1fr);
	padding               : 24px 24px;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 15px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.participate__left {
	display        : flex;
	flex-direction : column-reverse;
	grid-column    : 4 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : column;
		grid-column    : 6 span;
	}
}

.participate__title {
	color       : $black;
	font-size   : 22px;
	font-weight : 500;
	line-height : 32px;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
		max-width   : 510px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.title-bg {
		background-image      : url("../images/home/participate/bg-title.svg");
		background-position-y : 5px;
		margin                : 0 -15px;
		padding               : 0 20px;
	}
}

.participate__img {
	margin-bottom : 16px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.participate__right {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}

	.b-btn--red {
		min-width : 100%;
		padding   : 12px 32px;

		@media screen and (min-width : $break-table) {
			margin-top : 40px;
			min-width  : auto;
			padding    : 16px 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.participate-step {
	background-color : $white;
	border           : 1px solid $gray-stroke;
	border-radius    : 8px;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		display : flex;
		padding : 40px 40px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 20px 24px 24px 24px;
	}

	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 16px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.participate-step__icon {
	flex-shrink   : 0;
	height        : 48px;
	margin-bottom : 24px;
	width         : 48px;

	@media screen and (min-width : $break-table) {
		height        : 64px;
		margin-bottom : 0;
		width         : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.participate-step__wrap {


	@media screen and (min-width : $break-table) {
		margin-left : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.participate-step__name {
	background-image    : url("../images/home/participate/step-bg.svg");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : cover;
	color               : $black;
	display             : inline-block;
	font-size           : 14px;
	font-style          : normal;
	font-weight         : 500;
	line-height         : 20px;
	margin              : -9px -15px 24px;
	padding             : 9px 20px;

	@media screen and (min-width : $break-table) {
		font-size     : 20px;
		line-height   : 28px;
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.participate-step__text {
	color         : $black;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.participate-step__btn {
	color       : $red;
	font-size   : 14px;
	font-weight : 500;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.add-form {
	&__label {
		color       : $red;
		cursor      : pointer;
		font-size   : 14px;
		font-weight : 500;
		line-height : 20px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 28px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
