.last-news__wrapper {
	padding-bottom : 90px;
	position       : relative;

	@media screen and (min-width : $break-table) {
		padding-bottom : 0;
	}

	.b-btn--light {
		bottom    : 0;
		left      : 50%;
		min-width : 100%;
		position  : absolute;
		transform : translateX(-50%);

		@media screen and (min-width : $break-table) {
			min-width : auto;
			position  : unset;
			transform : unset;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.last-news__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}
}

.last-news__title {
	.title-bg {
		background-image : url("../images/home/last-news/bg-title.svg");
		margin           : 0 -10px;
		padding          : 0 20px;
	}
}

.last-news__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.news-item {
	display        : flex;
	flex-direction : column;
	grid-column    : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
	}

	&__img {
		margin-bottom : 24px;
		max-height    : 165px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			max-height : 224px;
		}

		img {
			border-radius : 8px;
			height        : 100%;
			object-fit    : cover;
			width         : 100%;
		}
	}

	&__text {
		color         : $black;
		font-size     : 16px;
		font-weight   : 500;
		line-height   : 24px;
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			font-size     : 20px;
			line-height   : 28px;
			margin-bottom : 40px;
		}

		@media screen and (min-width : $break-desktop) {

		}

	}

	&__link {
		align-items : center;
		color       : $red;
		display     : inline-flex;
		font-size   : 16px;
		font-weight : 500;
		line-height : 24px;
		margin-top  : auto;
		transition  : color .3s ease;

		&:hover {
			color : #820C12;

			svg {
				path {
					fill : #820C12;
				}
			}
		}

		svg {
			margin-left : 8px;

			path {
				fill       : $red;
				transition : fill .3s ease;
			}
		}
	}
}
