.work__grid--business {
	counter-reset         : count;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.card--business {
	grid-column : 4 span;
	margin      : 0;
	position    : relative;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
	}

	&:before {
		color             : #C3121B;
		content           : "0" counter(count);
		counter-increment : count;
		font-style        : normal;
		margin-bottom     : 16px;
		position          : absolute;
		right             : 24px;
		top               : 24px;
		z-index           : 1;
	}
}
