.programs__title {
	.title-bg {
		background-image      : url("../images/saving/programs/bg-title.svg");
		background-position-y : 10px;
		background-size       : contain;
		margin                : 0 -15px;
		padding               : 0 15px;
		position              : relative;
		z-index               : 2;

		@media screen and (min-width : $break-table) {
			margin  : 0 -18px;
			padding : 0 25px;
		}
	}
}

.programs__grid {
	margin : 0 -24px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}
}

.programs__item {
	background    : $gray;
	border-radius : 8px;
	margin        : 0 24px;
	padding       : 24px 24px;
	width         : 100%;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}
}

.programs__img {
	margin-bottom : 24px;
	text-align    : center;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}
}

.programs__wrap {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 16px;
		padding-left   : 16px;
	}
}

.programs__name {
	color         : $black;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 28px;
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 22px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.programs__list {
	li {
		color       : $black;
		font-size   : 14px;
		font-weight : 500;
		line-height : 20px;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			font-weight : 500;
			line-height : 24px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&:before {
			background-color : $red;
			border-radius    : 100%;
			content          : "";
			display          : inline-block;
			height           : 8px;
			margin-right     : 12px;
			width            : 8px;
		}

		&:not(:last-child) {
			margin-bottom : 12px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 16px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}

.swiper--programs {
	.swiper-wrapper {
		@media screen and (min-width : $break-table) {
			box-sizing            : unset;
			display               : grid;
			grid-gap              : 24px;
			grid-template-columns : repeat(12, 1fr);
			transform             : translate3d(0, 0, 0) !important;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.swiper-slide {
		display : flex;
		height  : auto;

		@media screen and (min-width : $break-table) {
			grid-column : 6 span;
			width       : 100%;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 4 span;
		}
	}
}
