.calculator__title {
	.title-bg {
		background-image      : url("../images/saving/programs/bg-title.svg");
		background-position-y : 5px;
		background-size       : contain;
		margin                : 0 -15px;
		padding               : 0 15px;
		position              : relative;
		z-index               : 2;

		@media screen and (min-width : $break-table) {
			margin  : 0 -18px;
			padding : 0 25px;
		}
	}
}

.calculator__wrapper {
	background-color      : $gray;
	border-radius         : 8px;
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);
	padding               : 24px 24px;

	@media screen and (min-width : $break-table) {
		grid-gap : 40px;
		padding  : 40px 40px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 64px;
		grid-template-columns : repeat(12, 1fr);
		padding               : 64px 64px;
	}
}

.calculator__left {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 7 span;
	}
}

.calculator__right {
	background-color      : $red;
	background-image      : url("../images/icon/rub.svg");
	background-position-x : right;
	background-repeat     : no-repeat;
	background-size       : contain;
	border-radius         : 8px;
	grid-column           : 4 span;
	margin                : 0 -24px;
	padding               : 24px 24px;

	@media screen and (min-width : $break-table) {
		margin  : 0 -40px;
		padding : 40px 40px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column;
		grid-column    : 5 span;
		margin         : 0;
	}

	.b-btn--white {
		grid-column : 2 span;
		margin-top  : auto;
		min-width   : 100%;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			min-width : auto;
		}
	}
}

.calculator__wrap {
	display               : grid;
	grid-gap              : 20px;
	grid-template-columns : repeat(2, 1fr);
	margin-bottom         : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		display       : block;
		margin-bottom : 0;
	}

}

.calculator__item {
	grid-column : 1 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	&:not(:last-child) {


		@media screen and (min-width : $break-desktop) {
			margin-bottom : 40px;
		}

	}
}

.calculator__name {
	color         : $white;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 8px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.calculator__value {
	color       : $white;
	font-size   : 24px;
	font-weight : 500;
	line-height : 32px;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.calculator__group {
	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 40px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.calculator__named {
	color         : $black;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.calculator__label {
	display        : flex;
	flex-direction : column;

	input {
		background-color : $white;
		border           : 1px solid rgba(0, 0, 0, 0.20);
		border-radius    : 8px;
		color            : $black;
		font-size        : 16px;
		font-weight      : 500;
		line-height      : 24px;
		padding          : 16px 16px;

		&:focus-visible {
			
		}


		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 28px;
			padding     : 18px 24px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&::placeholder {
			color       : $black;
			font-size   : 16px;
			line-height : 24px;

			@media screen and (min-width : $break-table) {
				font-size   : 20px;
				font-weight : 500;
				line-height : 28px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}


	}


}

.calculator__input {

}

.calculator__bottom {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-top      : 12px;

	@media screen and (min-width : $break-table) {
		margin-top : 16px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	span {
		color       : #000;
		font-size   : 12px;
		font-weight : 500;
		line-height : 16px;
		opacity     : 0.6;
	}
}


.range {
	width : 99%;
}

.noUi-target {
	background    : transparent;
	border        : none;
	border-radius : 4px;
	box-shadow    : none;
}

.range.noUi-horizontal {
	height : 4px !important;
}

.noUi-connect {
	background : $red;
}

.noUi-handle {
	background    : $red;
	border        : none;
	border-radius : 100%;
	box-shadow    : none;
	cursor        : pointer;

	&:after {
		display : none;
	}

	&:before {
		display : none;
	}
}

.noUi-horizontal .noUi-handle {
	height : 12px;
	right  : -8px;
	top    : -4px;
	width  : 12px;
}

.noUi-touch-area {
	height : 100%;
	width  : 100%;
}
