.section {
	margin : 42px 0;
	@media screen and (min-width : $break-table) {
		margin : 62px 0;
	}
	@media screen and (min-width : $break-desktop) {
		margin : 105px 0;
	}
}

.section-head {
	margin-bottom : 30px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}
}

.section-title {
	color       : $black;
	font-size   : 32px;
	font-weight : 500;
	line-height : 40px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 40px;
		line-height : 48px;
	}
}

.card-item {
	background-color : $gray;
	border-radius    : 8px;
	display          : flex;
	flex-direction   : column;
	padding          : 24px 24px;
	transition       : background-color .3s ease;

	@media screen and (min-width : $break-table) {
		padding : 40px 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:hover {
		background-color : $light-red;

		.card-item__link {
			color : $red-hover;

			svg {
				path {
					fill : $red-hover;
				}
			}
		}
	}

	&__icon {
		margin-bottom : 24px;
	}

	&__text {
		color         : $black;
		font-size     : 20px;
		font-weight   : 500;
		line-height   : 28px;
		margin-bottom : 20px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 40px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&__link {
		align-items : center;
		color       : #C3121B;
		display     : flex;
		font-size   : 16px;
		font-weight : 500;
		line-height : 24px;
		margin-top  : auto;
		transition  : color .3s ease;

		svg {
			margin-left : 8px;

			path {
				fill       : $red;
				transition : fill .3s ease;
			}
		}
	}
}

.grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(12, 1fr);
}

.card-item--conditions {
	grid-column : 3 span;

	.card-item__text {
		margin-bottom : 0;
	}

	&:hover {
		background-color : $gray;
	}
}

.card-item--loan {
	grid-column : 4 span;

	.card-item__text {
		margin-bottom : 0;
	}

	&:hover {
		background-color : $gray;
	}
}

.section-offset {
	padding : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 40px 0;
	}
}

.grid-section {
	margin : 0 -24px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}
}

.card {
	background-color : $gray;
	border-radius    : 8px;
	margin           : 0 24px;
	padding          : 24px 24px;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		margin  : 0;
		padding : 40px 40px;
	}
}

.card__icon {
	height        : 48px;
	margin-bottom : 24px;
	width         : 48px;

	@media screen and (min-width : $break-table) {
		height : 64px;
		width  : 64px;
	}
}

.card__text {
	color       : $black;
	font-size   : 16px;
	font-weight : 500;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
	}
}
