.contacts__gird {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 40px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 40px 24px;
	}
}

.contacts__title {

	.title-bg {
		background-image      : url("../images/home/contacts/bg-title.svg");
		background-position-y : 5px;
		margin                : 0 -15px;
		padding               : 0 20px;
	}

}

.contacts__map {
	border-radius : 8px;
	grid-column   : 4 span;
	overflow      : hidden;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.map {
	height : 312px;

	@media screen and (min-width : $break-table) {
		height : 440px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contacts__item {
	background-color : $gray;
	border-radius    : 8px;
	grid-column      : 4 span;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
		padding     : 40px 40px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}
}

.contacts__icon {
	height        : 48px;
	margin-bottom : 24px;
	width         : 48px;

	@media screen and (min-width : $break-table) {
		height : 64px;
		width  : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contacts__wrap {
	&:not(:last-child) {
		margin-bottom : 24px;
	}
}

.contacts__name {
	color         : #666;
	font-size     : 12px;
	font-weight   : 500;
	line-height   : 16px;
	margin-bottom : 4px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contacts__text {
	color       : $black;
	font-size   : 16px;
	font-weight : 500;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contacts__wrapper {
	align-items : center;
	display     : flex;
	flex-wrap   : wrap;

	a {
		color       : $black;
		font-size   : 16px;
		font-weight : 500;
		line-height : 24px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 28px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
