.footer {
	padding : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 40px 0 32px 0;
	}
}

.footer__copyright {
	color         : $gray-text;
	font-size     : 10px;
	font-weight   : 500;
	line-height   : 12px;
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {
		font-size   : 12px;
		line-height : 16px;
	}

	span {
		display        : block;
		padding-bottom : 24px;

		@media screen and (min-width : $break-table) {
			padding-bottom : 32px;
		}
	}
}

.footer__link {
	color         : $gray-text;
	font-size     : 10px;
	font-weight   : 500;
	line-height   : 12px;
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {
		font-size   : 12px;
		line-height : 16px;
	}
}
