  border: none;
  border-radius: 100%;
  box-shadow: none;
  cursor: pointer;
}
.noUi-handle:after {
  display: none;
}
.noUi-handle:before {
  display: none;
}

.noUi-horizontal .noUi-handle {
  height: 12px;
  right: -8px;
  top: -4px;
  width: 12px;
}

.noUi-touch-area {
  height: 100%;
  width: 100%;
}

